/***
    The new CSS reset - version 1.7.3 (last updated 7.8.2022)
    GitHub page: https://github.com/elad2412/the-new-css-reset
***/

/*
    Remove all the styles of the "User-Agent-Stylesheet", except for the 'display' property
    - The "symbol *" part is to solve Firefox SVG sprite bug
 */
*:where(:not(html, iframe, canvas, img, svg, video, audio):not(svg *, symbol *)) {
    all: unset;
    display: revert;
}

/* Preferred box-sizing value */
*,
*::before,
*::after {
    box-sizing: border-box;
}

/* Reapply the pointer cursor for anchor tags */
a, button {
    cursor: revert;
}

/* Remove list styles (bullets/numbers) */
ol, ul, menu {
    list-style: none;
}

/* For images to not be able to exceed their container */
img {
    max-width: 100%;
}

/* removes spacing between cells in tables */
table {
    border-collapse: collapse;
}

/* Safari - solving issue when using user-select:none on the <body> text input doesn't working */
input, textarea {
    -webkit-user-select: auto;
}

/* revert the 'white-space' property for textarea elements on Safari */
textarea {
    white-space: revert;
}

/* minimum style to allow to style meter element */
meter {
    -webkit-appearance: revert;
    appearance: revert;
}

/* reset default text opacity of input placeholder */
::placeholder {
    color: unset;
}

/* fix the feature of 'hidden' attribute.
   display:revert; revert to element instead of attribute */
:where([hidden]) {
    display: none;
}

/* revert for bug in Chromium browsers
   - fix for the content editable attribute will work properly.
   - webkit-user-select: auto; added for Safari in case of using user-select:none on wrapper element*/
:where([contenteditable]:not([contenteditable="false"])) {
    -moz-user-modify: read-write;
    -webkit-user-modify: read-write;
    overflow-wrap: break-word;
    -webkit-line-break: after-white-space;
    -webkit-user-select: auto;
}

/* apply back the draggable feature - exist only in Chromium and Safari */
:where([draggable="true"]) {
    -webkit-user-drag: element;
}

/************* Variables *************/

:root {
    --background-color: #000;
    --panel-background-color: #131516;
    --divider-color: #414346;
    --nav-background-color: #131516;
    --nav-background-color-active: #B5C1CE;
    --subnav-background-color: #1C1F20;

    --text-color: #B5C1CE;
    --secondary-text-color: #727880;
    --headline-text-color: #fff;
    --subheadline-text-color: #fff;
    --nav-item-color: #727880;
    --nav-item-color-active: #131516;

    --border-radius: 3px;

    --body-font-size: 17px;
    --secondary-font-size: 14px;
    --headline-font-size: 30px;
    --subheadline-font-size: 21px;
    --nav-font-size: 12px;

    --body-font-family: 'Averia Serif Libre', cursive;
    --secondary-font-family: 'Karla', sans-serif;
}

/************* General Styles *************/

body {
    margin: 0;
    font-family: 'Averia Serif Libre', cursive;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: var(--background-color);
    color: var(--text-color);
}

body {
  -ms-overflow-style: none; /* for Internet Explorer, Edge */
  scrollbar-width: none; /* for Firefox */
  overflow-x: hidden;
}

body::-webkit-scrollbar {
  display: none !important; /* for Chrome, Safari, and Opera */
}

h2 {
    margin-bottom: 20px;
    font-size: var(--headline-font-size);
    color: var(--headline-text-color);
    font-style: italic;
    font-weight: bold;
}

h3 {
    margin-bottom: 10px;
    font-size: var(--subheadline-font-size);
    color: var(--subheadline-text-color);
    font-weight: bold;
}

strong { font-weight: bold; }

/************* Styling *************/

.container {
    padding: 20px 20px 240px;
}

.item {
    margin-bottom: 30px;
}

.item-detail {
  display: block;
  margin-bottom: 10px;
  font-family: var(--secondary-font-family);
  font-size: var(--secondary-font-size);
  color: var(--secondary-text-color);
}

.line, .line-bottom {
  padding-bottom: 30px;
  border-bottom: 1px solid var(--divider-color);
}

.item-paragraph {}

.mb20 { margin-bottom: 20px;}

.divider {
  height: 1px;
  margin: 30px 0;
  background: var(--divider-color);
}

/*** Pill ***/

.pills {
  position: fixed;
  bottom: 69px;
  left: 0;
  width: 100%;
  height: 124px;
  padding: 8px;
  background: var(--subnav-background-color);
  box-shadow: 0px -3px 6px rgba(0, 0, 0, 0.8);
}

.row {
  overflow-y: hidden;
  white-space: nowrap;
}

.pills .first {
  margin-bottom: 8px;
}

.pill {
  display: inline-block;
  height: 50px;
  margin-right: 4px;
  padding: 0 15px;
  border: 2px solid #131516;
  border-radius: 100px;
  font-family: var(--secondary-font-family);
  font-size: var(--nav-font-size);
  letter-spacing: 1px;
  text-transform: uppercase;
  font-weight: bold;
}

.pills .active {
  background: var(--nav-background-color);
  color: var(--headline-text-color);
}

.pills .thumb {
  vertical-align: middle;
  margin: 0 4px 4px 0;
  width: 24px;
}

.pills .name {
  line-height: 47px;
}

/*** Warscroll stats ***/

.warscroll_stats {
  width: 100%;
  margin-bottom: 16px;
  padding: 10px;
  border-radius: var(--secondary-text-color);
  background: var(--panel-background-color);
  display: inline-grid;
  grid-template-columns: repeat(4, 75px [col-start]);
}

.warscroll_stats li {
  justify-self: center;
  display: block;
  width: 100%;
  text-align: center;
}

.warscroll_stats .key {
  display: block;
  font-size: var(--secondary-font-size);
  color: var(--secondary-text-color);
}

.warscroll_stats .value {
  font-size:  var(--headline-font-size);
  font-style: italic;
}

/*** Wizard ***/

.wizard {
  margin-bottom: 30px;
  padding: 10px;
  background: var(--panel-background-color);
  border-radius: var(--secondary-text-color);
}

.wizard .headline {
  margin-bottom: 5px;
  font-size:  var(--subheadline-font-size);
  background: var(--panel-background-color);
}

.wizard .text {
  font-size: var(--secondary-font-size);
  color: var(--secondary-text-color);
}

.wizard .icon {
  float: left;
  margin: 9px 16px 0 0;
}

/*** Attacks ***/

.attack {

}

.attack .name {
  font-size: var(--subheadline-font-size);
  color: var(--subheadline-text-color);
}

.attack ul {
  display: inline-grid;
  grid-template-columns: repeat(6, 56px [col-start]);
}

.attack li {
  justify-self: center;
  display: block;
  width: 100%;
  text-align: center;
}

.attack .key {
  display: block;
  font-size: var(--secondary-font-size);
  color: var(--secondary-text-color);
}

.attack .value {
  font-size: var(--body-font-size);
}

/************* Bottom nav *************/

.bottom_nav {
    position: fixed;
    bottom: 0px;
    width: 100%;
    height: 69px;
    background: var(--nav-background-color);
    font-size: var(--nav-font-size);
    font-family: var(--secondary-font-family);
}

.bottom_nav ul {
    display: inline-grid;
    grid-template-columns: repeat(5, 75px [col-start]);
}

.bottom_nav ul li {
    justify-self: center;
    display: block;
    width: 100%;
    text-align: center;
}

.bottom_nav a {
    display: block;
    height: 69px;
    padding-top: 13px;
}

.bottom_nav .active {
    color: #131516;
    background: #B5C1CE;
    font-weight: bold;
}

.bottom_nav .active .icon {
  color: red;
}
